import {
  beforeEach,
  beforeEachProviders,
  describe,
  expect,
  it,
  inject,
} from '@angular/core/testing';
import { ComponentFixture, TestComponentBuilder } from '@angular/compiler/testing';
import { Component } from '@angular/core';
import { By } from '@angular/platform-browser';
import { MediaplanSummaryComponent } from './mediaplan-summary.component';

describe('Component: MediaplanSummary', () => {
  let builder: TestComponentBuilder;

  beforeEachProviders(() => [MediaplanSummaryComponent]);
  beforeEach(inject([TestComponentBuilder], function (tcb: TestComponentBuilder) {
    builder = tcb;
  }));

  it('should inject the component', inject([MediaplanSummaryComponent],
      (component: MediaplanSummaryComponent) => {
    expect(component).toBeTruthy();
  }));

  it('should create the component', inject([], () => {
    return builder.createAsync(MediaplanSummaryComponentTestController)
      .then((fixture: ComponentFixture<any>) => {
        let query = fixture.debugElement.query(By.directive(MediaplanSummaryComponent));
        expect(query).toBeTruthy();
        expect(query.componentInstance).toBeTruthy();
      });
  }));
});

@Component({
  selector: 'test',
  template: `
    <app-mediaplan-summary></app-mediaplan-summary>
  `,
  directives: [MediaplanSummaryComponent]
})
class MediaplanSummaryComponentTestController {
}

